@import '../base.scss';

.c-article {

  &__banner {
    padding-bottom: 40.625rem;
    position: relative;
    margin-bottom: 3.5rem;

    &-image {
      position: absolute;
      object-fit: cover;
      height: 100%;
    }

    &-inner {
      @include t-absolute-center-x-y;
      max-width: 63.5625rem;
      width: 85%;
      text-align: center;

      &>* {
        color: $s-color--font-6;
      }
    }
  }

  &__tags {
    margin-bottom: 1.375rem;

    &-tag {
      margin-right: 0.375rem;

      &:last-child {
        margin-right: unset;
      }
    }
  }

  &__heading {
    margin-bottom: 1.375rem;
  }

  &__excerpt {
    //@include t-gutters;
    text-align: center;
    margin: 0 auto 0.875rem;
    max-width: 57.875rem;
    //width: 80%;
  }

  &__content {
    @include t-gutters;
    &-content {
      

      max-width: 59.125rem;
      margin-left: auto;
      margin-right: auto;

      ul,
      ol {
        padding-left: 2rem;
        list-style-type: circle;
        margin-bottom: 1rem;
      }

      li {
        @include t-font--type-base;
        color: $s-color--font-8;
      }

      h1,
      h2,
      h3,
      h4,
      h5 {
        @include t-font--heading-5;
        margin-bottom: 2.0625rem;
        padding-top: 2.25rem;
      }

      p {
        margin-bottom: 4rem;
        line-height: 1.125rem;

        &:last-child {
          margin-bottom: 6.25rem;
        }
      }

      >p,
      >ul,
      >ol,
      >h1,
      >h2,
      >h3,
      >h4,
      >h5 {
        max-width: 59.125rem;
        margin-left: auto;
        margin-right: auto;
      }
    }

    &-images {
      display: flex;
      align-items: center;
      margin-bottom: 2.1875rem;
      margin-top: 8.9375rem;
    }

    &-image-container {
      &.is-left {
        padding-bottom: 71%;
        width: 54%;
        margin-bottom: 3.75rem;
      }

      &.is-right {
        padding-bottom: 104%;
        width: 100%;
      }
    }

    &-image {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &-video {
      padding-top: 2.4375rem;
      position: relative;
      cursor: pointer;
      overflow: hidden;

      &>* {
        pointer-events: none;
      }

      &:hover {

        .c-article__content-video-play-button {
          transform: scale(1);
        }
      }

      &-inner {
        @include t-absolute-center-x-y;
        width: 100%;
        text-align: center;
        z-index: 1;
      }

      &-image-container {
        padding-bottom: 130%;
      }

      &-title {
        width: 80%;
        color: $s-color--font-6;
        margin: 0 auto 1.875rem;
      }

      &-play-button {
        @include t-width-and-height(73);
        display: block;
        margin: 0 auto;
        transform: scale(.9);
        transition: transform $s-anim--time-short $s-anim--ease-in-out;

        .icon--play {
          &-inner {
            fill: $s-color--background-tertiary;
          }
        }
      }


      &__image {
        object-fit: cover;
        @include t-absolute-full;
      }
    }
  }

  @include t-media-query($s-small-down) {
    &__banner-image {
      &.is-desktop {
        display: none;

        .has-desktop-only & {
          display: block;
        }
      }
    }

    &__content {
      &-images {
        flex-direction: column;
      }

      &-video-image-container {
        padding-bottom: 56%;
      }
    }

  }

  @include t-media-query($s-small-up) {
    &__banner {
      padding-bottom: 34.375rem;
      margin-bottom: 6.25rem;

      &-image {
        &.is-mobile {
          display: none;
        }

        &.is-desktop {
          display: block;
        }
      }

      &-inner {
        width: 50%;
        max-width: 62.5rem;
      }
    }

    &__excerpt {
      margin-bottom: 6.25rem;
    }

    &__content {
      &-content {

        >p,
        >h1,
        >h2,
        >h3,
        >h4,
        >h5 {
          //width: 70%;
        }
      }

      &-images {
        flex-direction: row;
        justify-content: space-between;
      }

      &-image-container {
        &.is-left {
          width: 38%;
          margin-bottom: 0;
          padding-bottom: 56%;
        }

        &.is-right {
          width: 58%;
          padding-bottom: 69%;
        }
      }

      &-video-image-container {
        padding-bottom: 55%;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &__excerpt {
      max-width: 57.875rem;
    }

    &__banner {
      padding-bottom: t-to-rem(650);
    }

    &__content {
      &-content {
        p {
          line-height: 1.625rem;
        }
      }

      &-video-play-button {
        width: t-to-rem(87);
        height: t-to-rem(87);
      }
    }

  }

  @include t-media-query($s-large-up) {
    &__content {
      &-image-container {
        &.is-left {
          width: 38%;
          margin-bottom: 0;
          padding-bottom: 40%;
        }

        &.is-right {
          width: 55%;
          padding-bottom: 50%;
        }
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__banner {
      padding-bottom: 51.875rem;
      margin-bottom: 6.9375rem;
    }

    &__excerpt {
      margin-bottom: 4rem;
    }

    &__content {

      h1,
      h2,
      h3,
      h4,
      h5 {
        margin-bottom: 1.875rem;
        padding-top: 2.375rem;
      }

      &-images {
        margin-top: 11.5625rem;
        margin-bottom: 7.75rem;
      }

      &-image-container {
        &.is-left {
          width: 26.5%;
          padding-bottom: 35%;
        }

        &.is-right {
          width: 49.5%;
          padding-bottom: 51%;
        }
      }

      &-video {
        max-width: 125rem;
        margin: 0 auto;
        padding-top: 6.5625rem;
      }

      &-video-title {
        margin-bottom: t-to-rem(50);
      }

      &-video-image-container {
        padding-bottom: 38%;
      }
    }
  }
}